"use strict";
import React, {Component, PropTypes} from 'react'
import './GetTicket.scss'
import IsPcOrNot from '../../../components/lib/IsPcOrNot.js'
import Constant from '../../../components/lib/Constant.js'
import {browserHistory} from 'react-router'
import Params from '../../../components/lib/QueryParams.js'
import en from '../../../locale/en';
import zh from '../../../locale/zh';
import $ from 'n-zepto'
import './multiple-select'
import Header from '../../../components/Header/Header'
import Footer from '../../../components/Footer/Footer'
import Card from '../assets/card.png'
import Email from '../assets/email.png'
import Name from '../assets/name.png'
import PhoneIcon from '../assets/phone.png'
import Photo from '../assets/photo.png'
import Company from '../assets/school.png'
import Position from '../assets/work.png'

import School from '../assets/school.png'
import Subject from '../assets/subject.png'
import Loading from '../assets/loading.gif'
import ImageSrc from '../assets/image.png'

import {Menu, FormattedMessage} from '../../../components/lib/BaseComponent'
import {InputLine, SelectLine, ImageLine, CheckBoxLine, RadioLine} from '../../../components/lib/BaseForm'

let messages = {};
messages['en'] = en;
messages['zh-CN'] = zh;
messages['zh-cn'] = zh;


class Title extends Component {
    render() {
        return <div style={IsPcOrNot.get() ? {
            fontSize: "24px",
            fontWeight: "600",
            margin: "60px 0 20px 0"
        } : {
            fontSize: "16px",
            fontWeight: "600",
            margin: "40px 20px 20px 20px"
        }}>
            <FormattedMessage id="members_info"/>
        </div>
    }
}

class Tip extends Component {
    render() {
        return <div style={IsPcOrNot.get() ? {
            fontSize: "12px",
            paddingBottom: "80px"
        } : {
            fontSize: "12px",
            margin: "0 20px"
        }}>
            <FormattedMessage id="student_check_tip"/>
        </div>
    }
}


class Form extends Component {
    constructor(props) {
        super(props);
        this.type = null
        this.form_names = ['name', 'phone', 'email', 'sex', 'ageLevel', 'tribune', 'headImg', 'company', 'companySize', 'position', 'positionSelect', 'whetherUseAi',
            'useAiSelect', 'whetherUseBaiduAi', 'useBaiduAiSelect', 'futureAiPlanSelect', 'whetherFirstAttend', 'likeTribuneSelect',
            'gainsResultSelect', 'type'];
        this.time = {
            "seconds": 60
        };
        this.form_data = {};
        this.error = {
            "text": 'sure'
        }
        this.sex = [{value: '', text: messages[localStorage['language']]['sex1']}, {
            value: '',
            text: messages[localStorage['language']]['sex2']
        }];
        this.noOrYes = [{value: '1', text: messages[localStorage['language']]['yes']}, {
            value: '0',
            text: messages[localStorage['language']]['no']
        }]
        this.typeList = [{value: '0', text: messages[localStorage['language']]['developer_ticket_19']}, {
            value: '3',
            text: messages[localStorage['language']]['sub_forum_ticket_2019']
        }]
        this.ageLevel = [];
        this.tribune = [];
        this.companySize = [];
        this.positionSelect = [];

        for (var i = 1; i < 6; i++) {
            this.ageLevel.push({
                value: i + '',
                text: messages[localStorage['language']]['ageLevel' + i]
            })
        }
        for (var i = 1; i < 9; i++) {
            this.tribune.push({
                value: i + '',
                text: messages[localStorage['language']]['sub_forums' + i]
            })
        }
        for (var i = 1; i < 8; i++) {
            this.companySize.push({
                value: i + '',
                text: messages[localStorage['language']]['companySize' + i]
            })
        }
        for (var i = 1; i < 14; i++) {
            this.positionSelect.push({
                value: i + '',
                text: messages[localStorage['language']]['positionSelect' + i]
            })
        }
        //
        this.useAiSelectData = [];
        this.futureAiPlanSelectData = [];
        this.likeTribuneSelectData = [];
        this.gainsResultSelectData = [];
        this.positonSelectData = [];
        this.useBaiduAISelectData = [];
        for (let i = 1; i < 16; i++) {
            this.useBaiduAISelectData.push({
                'value': i + '',
                'text': 'useBaiduAiSelect' + i
            })
        }
        for (let i = 1; i < 14; i++) {
            this.positonSelectData.push({
                'value': i + '',
                'text': 'positionSelect' + i
            })
        }
        for (let i = 1; i < 13; i++) {
            this.useAiSelectData.push({
                'value': i + '',
                'text': 'useAiSelect' + i
            })
        }
        for (let i = 1; i < 14; i++) {
            this.futureAiPlanSelectData.push({
                'value': i + '',
                'text': 'useAiSelect' + i
            })
        }
        for (let i = 1; i < 10; i++) {
            this.likeTribuneSelectData.push({
                'value': i + '',
                'text': 'likeTribuneSelect' + i
            })
        }
        for (let i = 1; i < 7; i++) {
            this.gainsResultSelectData.push({
                'value': i + '',
                'text': 'gainsResultSelect' + i
            })
        }

    }


    componentDidMount() {
        if (Params('type')) {
            this.form_data.type = Params('type');
            this.setState({
                form_data: this.form_data
            })
        }
        if (Params('id')) {
            $.get(Constant.url + '/queryTicketById.do?id=' + Params('id'), (data) => {
                this.form_data = data.data;
                if (this.form_data) {
                    this.setState({
                        form_data: this.form_data
                    });
                }
            });
        }
        if (Params('c')) {
            setTimeout(function () {
                $.get(Constant.url + '/hasLoginBaidu.do', (data) => {
                    if (data.error_no !== '0') {
                        IsPcOrNot.get() ? window.instance.show() : window.instance.render($("#login")[0]);
                    }
                })
            }, 3000);
        }
    }


    submit = () => {
        let flag = true;
        for (let i = 0; i < this.form_names.length; i++) {
            if (this.refs[this.form_names[i]] !== undefined) {
                if (!this.refs[this.form_names[i]].valid()) {
                    flag = false;
                    break;
                }
            }
        }
        if (flag) {
            $.post(Constant.url + 'ticketInfo.do', this.form_data, (data) => {
                if (data.error_no === '0') {
                    //TODO success call back
                    this.error.text = data.data;
                    browserHistory.push(Constant.router_preffix + '/order?type=' + this.form_data.type)
                }
                else {
                    this.error.text = data.data;
                    this.setState(this.error);
                    this.refs.Menu.open();
                }
            });
        }
    };
    refresh = () => {
        this.setState(this.form_data);
    }

    render() {
        return (
            <div>
                <Title>
                    <FormattedMessage id="members_information"/>
                </Title>
                <Menu ref="Menu" text={this.error.text}/>
                <Tip/>
                <table cellPadding='0' cellSpacing='0' className={IsPcOrNot.get() ? "form-table" : 'm-table'}>
                    <tbody>
                    <tr>
                        <td>
                            <FormattedMessage id="checkTicketType"/>
                        </td>
                        <td>

                            <RadioLine data={this.typeList} name='type' form={this.form_data}/>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <FormattedMessage id="base_info"/>
                        </td>
                        <td>
                            <InputLine form={this.form_data} ref={this.form_names[0]} text="name" icon={Name}
                                       name={this.form_names[0]}
                                       valid="notNull||name"/>
                            <InputLine form={this.form_data} ref={this.form_names[1]} text="phone" icon={PhoneIcon}
                                       name={this.form_names[1]}
                                       valid="notNull"/>
                            <InputLine form={this.form_data} ref={this.form_names[2]} text="email" icon={Email}
                                       name={this.form_names[2]}
                                       valid="notNull"/>
                            <RadioLine form={this.form_data} ref={this.form_names[3]} text="sex" data={this.sex}
                                       name={this.form_names[3]} valid="notNull"/>

                            <SelectLine form={this.form_data} ref={this.form_names[4]} text="ageLevel"
                                        data={this.ageLevel}
                                        name={this.form_names[4]} valid="notNull"/>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <FormattedMessage id="interested"/>
                        </td>
                        <td>
                            <SelectLine form={this.form_data} ref={this.form_names[5]}
                                        text="choose_the_tribune_you_interested" data={this.tribune}
                                        name={this.form_names[5]} valid="notNull"/>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <FormattedMessage id="upload_avatar"/>
                        </td>
                        <td>
                            <ImageLine form={this.form_data} photo={Photo} ImageSrc={ImageSrc} loading={Loading}
                                       ref={this.form_names[6]} text="card_picture" name={this.form_names[6]}
                                       valid="notNull"/>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <FormattedMessage id="profession_info"/>
                        </td>
                        <td>
                            <InputLine form={this.form_data} ref={this.form_names[7]} text="company" icon={Company}
                                       name={this.form_names[7]}
                                       valid="notNull"/>
                            <SelectLine form={this.form_data} ref={this.form_names[8]} data={this.companySize}
                                        text="companySize" name={this.form_names[8]}
                                        valid="notNull"/>
                            <InputLine form={this.form_data} ref={this.form_names[9]} text="job" icon={Position}
                                       name={this.form_names[9]}
                                       valid="notNull"/>
                            <SelectLine form={this.form_data} ref={this.form_names[10]} data={this.positionSelect}
                                        text="positionSelect" name={this.form_names[10]}
                                        valid="notNull"/>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <FormattedMessage id="about_ai"/>
                        </td>
                        <td>
                            <RadioLine form={this.form_data} ref={this.form_names[11]}
                                       refresh={this.refresh.bind(this)}
                                       text="whetherUseAi" data={this.noOrYes}
                                       name={this.form_names[11]} valid="notNull"/>
                            {
                                this.form_data['whetherUseAi'] == "1" ?
                                    <CheckBoxLine form={this.form_data} ref={this.form_names[12]} text="useAiSelect"
                                                  name={this.form_names[12]} data={this.useAiSelectData}
                                                  valid="notNull"/> : null

                            }
                            {
                                this.form_data['whetherUseAi'] == '1' ?
                                    <RadioLine form={this.form_data} ref={this.form_names[13]}
                                               refresh={this.refresh.bind(this)} data={this.noOrYes}
                                               text="whetherUseBaiduAi" name={this.form_names[13]}
                                               valid="notNull"/> : null

                            }
                            {
                                (this.form_data['whetherUseBaiduAi'] == '1' && this.form_data['whetherUseAi'] == "1") ?
                                    <CheckBoxLine form={this.form_data} ref={this.form_names[14]}
                                                  text="useBaiduAiSelect"
                                                  name={this.form_names[14]} data={this.useBaiduAISelectData}
                                                  valid="notNull"/> : null

                            }
                            <CheckBoxLine form={this.form_data} ref={this.form_names[15]} text="futureAiPlanSelect"
                                          name={this.form_names[15]} data={this.futureAiPlanSelectData}
                                          valid="notNull"/>
                        </td>
                    </tr>
                    <tr>
                        <td>
                            <FormattedMessage id="join_info"/>
                        </td>
                        <td>
                            <RadioLine form={this.form_data} ref={this.form_names[16]} text="whetherFirstAttend"
                                       name={this.form_names[16]} data={this.noOrYes}
                                       valid="notNull"/>
                            <CheckBoxLine form={this.form_data} ref={this.form_names[17]} text="likeTribuneSelect"
                                          name={this.form_names[17]} data={this.likeTribuneSelectData}
                                          valid="notNull"/>
                            <CheckBoxLine form={this.form_data} ref={this.form_names[18]} text="gainsResultSelect"
                                          name={this.form_names[18]} data={this.gainsResultSelectData}
                                          valid="notNull"/>
                        </td>
                    </tr>
                    </tbody>
                </table>
                <div className="tribune-selector" style={{marginTop: IsPcOrNot.get() ? '60px' : '0'}}>
                    <div style={{margin: IsPcOrNot.get() ? "40px 0 80px 0" : '0px 0 80px 0', textAlign: "center"}}>
                        <button type="submit" className="base_type_btn"
                                style={{padding: "15px 100px", borderRadius: "3px", marginTop: "40px"}}
                                onClick={this.submit}>
                            <FormattedMessage id="submit"/>
                        </button>
                    </div>
                </div>

            </div>
        )
    }
}

class Phone extends Component {
    render() {
        return <div style={{backgroundColor: "#fff"}}>
            <Header/>
            <Form/>
            <Footer/>
        </div>
    }
}

class PC extends Component {
    render() {
        return <div style={{backgroundColor: "#fff"}}>
            <Header/>
            <div style={{margin: "0 auto", display: "block", position: "relative", width: "1200px"}}>
                <Form/>
            </div>
            <Footer/>
        </div>
    }
}

export class GetTicket extends Component {
    constructor(props) {
        super(props);
    }

    changeLanguage = () => {
        if (localStorage['language'] == 'en') {
            localStorage['language'] = 'zh-CN';
        }
        else {
            localStorage['language'] = 'en';
        }
        location.href = location.href;
    };
    getLoginStatus = () => {
        let logined = false;
        $.get(Constant.url + 'hasLoginBaidu.do', (data) => {
            if (data.error_no == '0') {
                logined = false;
            }
            else {
                logined = true;
            }
        })
    }

    componentDidMount() {
        this.getLoginStatus();

    }

    login = () => {
        IsPcOrNot.get() ? window.instance.show() : window.instance.render($("#login")[0]);
    };

    render() {
        return <div>
            {
                IsPcOrNot.get() ?
                    <PC/> : <Phone/>
            }
        </div>
    }
}

export default GetTicket;


